.header {position: absolute;
    top: 0;
    left: 0;
    margin-top: 34px;
    width: 100%;
    z-index: 9999;
    .nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #fff;
        &__social {
            margin-bottom: 30px;
        }
        &__menu {
            display: flex;
            gap: 80px;
            margin-bottom: 30px;
            &-link {
                &::after {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: block;
                    width: 0%;
                    height: 23px;
                    border-bottom: 2px solid #cdaa7d;
                }
                &:hover,
                &:hover::after {
                    @include animation_links;
                }
            }
        }
        .logo {
            margin-bottom: 30px;
        }
        &__burger {
            margin-bottom: 30px;
            &-icon {
                cursor: pointer;
                position: relative;
                width: 50px;
                height: 40px;
                display: flex;
                align-items: center;
                .lines {
                    position: relative;
                    display: block;
                    width: 40px;
                    height: 2px;
                    background: #fff;
                    &::after,
                    &::before {
                        content: "";
                        position: absolute;
                        display: block;
                        width: 40px;
                        height: 2px;
                        background-color: #fff;
                    }
                    &::after {
                        top: 10px;
                        left: 10px;
                    }
                    &::before {
                        bottom: 10px;
                        left: 10px;
                    }
                }
                &:hover  >.lines,&:hover  .lines::after,&:hover > .lines::before{
                    transition: 0.5s;
                    background: #cdaa7d;
                }
                &:hover  >.lines{
                    animation: burger-Center-line 2s forwards;
                    @keyframes burger-Center-line {
                        from{

                        }
                        to{
                            width: 0;
                        }
                    }
                }
             

                &:hover  .lines::after,&:hover > .lines::before{
                    animation: burger-line 2s forwards;
                    @keyframes burger-line {
                        from{

                        }
                        to{
                            width: 0;
                        }
                    }
                }
            }
           
        }
    }
}
