.services {
    width: 100%;
    margin-top: 95px;
}
.serv {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 24px;
    row-gap: 60px;
    &-card {
        width: 416px;
    }
    &-pic {
        position: relative;
        &::after {
            display: block;
            content: "";
            position: absolute;
            top: -20px;
            left: 20px;
            width: 396px;
            height: 300px;
            border: 1px solid #fff;
            z-index: -1;
        }
    }
    &-desc {
        margin-top: 30px;
        color: #fff;
    }
}
