a {
	text-decoration: none;
	color: #000;
	margin: 0;
	&:hover {
		color: #cdaa7d;
	}
}

.container {
	padding: 0 calc((50% - 648px) / $c);
}

.nav__menu-link,
.present-text,
.textContent,
.service-title,
.works-title,
.filter-title,
.footer-title,
.footer-text {
    @include fonts(2.4rem, 700, 120%, #ffffff);
}

.nav__menu-link {
    position: relative;
    @include fonts(1.8rem, 600, 130%, #ffffff);

}
.nav__social {
    @include fonts(2.4rem, 600, 130%, #ffffff);
}
.present-text {
    font-size: 7.2rem;
}

.textContent {
    font-size: 2.7rem;
    line-height: 130%;
    text-align: center;
}
.service-title {
    font-weight: 400;
    font-size: 3rem;
}
.works-title {
    font-size: 4.8rem;
    text-align: center;
    color:#ffffff;
}
.filter-title {
    font-weight: 400;
    line-height: 130%;
    text-align: center;
    color:#ffffff;
}
.footer-text {
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 130%;
}