$c:1;

@mixin font-face($name, $file, $weight: 400, $style: normal) {
	@font-face {
		font-family: "#{$name}";
		src: local("#{$file}"), url("../fonts/#{$file}.woff2") format("woff2"),
			url("../fonts/#{$file}.woff") format("woff");
		font-weight: $weight;
		font-style: $style;
		font-display: swap;
	}
}

@mixin fonts( $fz, $weight: 400, $lh, $color) {
	
	font-size: $fz;
	font-weight: $weight;
	line-height: $lh;
	color: $color;
	font-display: swap;
}

@mixin bg($size: "contain", $position: "center") {
	background-size: #{$size};
	background-position: #{$position};
	background-repeat: no-repeat;
}

@mixin btn_anim1($scaleMax: 1.05, $scaleMin: 0.95) {
	transform-origin: center center;
	transition: all ease-out 240ms;

	&:hover {
		transform: scale(#{$scaleMax});
	}

	&:focus {
		outline: transparent;
	}

	&:focus-visible {
		transform: scale(#{$scaleMax}) translateY(-5%);
	}

	&:active {
		transform: scale(#{$scaleMin});
	}
	
}

@mixin centerEl() {
	display: flex;
	justify-content: center;
	align-items: center;
}

@mixin no-btn($display: "inline-block") {
	padding: 0;
	margin: 0;
	border: 0;
	background-color: transparent;
	border-radius: 0;
	cursor: pointer;
	appearance: none;
	display: #{$display};
}

@mixin transit($tr: 0.24) {
	transition: all #{$tr}s;
}

@mixin container($max-width: "120rem", $padding: "2rem") {
	width: 100%;
	max-width: #{$max-width};
	padding: 0 #{$padding};
	margin: 0 auto;
}

//GRID

@mixin GridEl($count-columns,$sz-columns,$count-rows,$sz-rows) {
	display: grid;
        grid-template-columns: repeat($count-columns,$sz-columns);
        grid-template-rows: repeat($count-rows,$sz-rows);
}

@mixin Grid-Position($columnST,$columnE,$rowST,$rowE) {
	grid-column-start: $columnST;
	grid-column-end: $columnE;
	grid-row-start: $rowST;
	grid-row-end: $rowE;

}

.bm{
	mix-blend-mode: difference;
        text-shadow: 0 0 2px #000;
}

//anim

@mixin BTN-Psfer($del: 1, $fz: 22px) {
	.contactUse {
		@include centerEl;
		box-sizing: border-box;
		min-width: calc(200px / $del);
		min-height: calc(200px / $del);
		background-color: #ffffff;
		border: 8px solid #0c0c0c31;
		outline: 8px solid #ffffff51;
		border-radius: 50%;
		cursor: pointer;
		p {
			max-width: 100px;
			text-align: center;
			color: #020a0c;
			font-size: $fz;
		}
	}
	@include btn_anim1;

	&:hover > .contactUse {
		transition: 0.5s;
		border: 15px solid #0c0c0c31;
		outline: 20px solid #ffffff51;
		box-shadow: 0 0 20px 30px #0302587d;
	}
	.contactUse::after {
		display: none;
		content: " ";
		position: absolute;
		width: calc(150px / $del);
		height: calc(80px / $del);
		border-radius: 50%;
		box-shadow: 0 0 20px 30px  #dd9802;//rgb(19, 251, 2)
		background-color:  #dd9802;
		z-index: -1;
	}

	&:hover > .contactUse::after {
		display: block;
		animation: animRotat 3s cubic-bezier(0.575, 0.885, 0.52, 1.275) infinite forwards;
		@keyframes animRotat {
			0% {
				transform: rotate(0deg);
			}
			50%{
				transform: rotate(180deg);
				width: calc(190px / $del);
				height: calc(190px / $del);
				box-shadow: 0 0 20px 30px  rgb(19, 251, 2);
			}

			100% {
				transform: rotate(0deg);
				width: calc(150px / $del);
				height: calc(80px / $del);
				box-shadow: 0 0 20px 30px  #dd9802;
			}
		}
	}
	&:not(.black-spher:hover) > .contactUse::after {
		transition: 2s;
		width: 100%;
		height: 1px;
	}
}

@mixin animation_links{
	transition: .3s;
	width: 100%;
}
