.owner {
    width: 100%;
    min-height: 100%;
    max-height: 100%;
    background: no-repeat url("../../../../img/bgc.png");
    background-size: 100%;
    background-position-y: bottom;
    display: flex;
    align-items: center;
    flex-direction: column;
    row-gap: 230px;
     
}
.present-text {
    width: 550px;
    margin: 0 auto;
    text-align: center;
    margin-top: calc(260px / 1.25);
}
.scroll {
    // margin-top: 290px;
    margin-bottom: 100px;
    display: flex;
    &-pic {
        width: 20px;
        height: 30px;
        margin-right: 30px;
    }
    &-text {
        font-size: 1.8rem;
        color: #fff;
    }
}
