.works {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 168px;
    &-title {
        text-align: center;
    }
    .filter-menu {
        display: flex;
        margin-top: 80px;
        gap: 30px;
        &--link {
            position: relative;
            display: block;

            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                display: block;
                width: 0%;
                height: 31px;
                border-bottom: 2px solid #cdaa7d;
                margin-top: 5px;
            }
            &:hover,
            &:hover::after {
                @include animation_links;
            }
        }
    }
}

.ourWorks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 24px;
    margin-top: 55px;
    &-pic {
    }
}
